import React, { useState } from 'react'
// import { UserContext } from '../context/Fetch';

function Navbar({data, setData}) {

  const [search, setSearch] = useState('');


  const handleChange = e => {
    setSearch((s) => e.target.value)
  }

  const handleSubmit = (e) => { 
    e.preventDefault()
    const filteredData = data.filter(game => game.Topic.toLowerCase().includes(search.toLowerCase()))
    setData(filteredData)
  }

  const handleAcad = (e) =>{
    e.preventDefault()
    const filterAcadGroup = data.filter(acad => acad.Group.includes("Academic"))
    setData(filterAcadGroup)
  }
const handleFinance = (e) =>{
  e.preventDefault()
  const filterFinGroup = data.filter(fin => fin.Group.includes("Financial Literacy"))
  setData(filterFinGroup)
}
const handleStageOne = (e) => {
  e.preventDefault()
  const filterLevelOne = data.filter(fin => fin.Level.includes("Key Stage 1" ))
  setData(filterLevelOne)

}
const handleStageTwo = (e) => {
  e.preventDefault()
  const filterLevelTwo = data.filter(fin => fin.Level.includes("Key Stage 2" ))
  setData(filterLevelTwo)
}
 
  return (
    <div className="navbar">
      <div className="left">
        <a href="/n">9ija Kids Game</a>
      </div>
      <div className="middle">
<h3>Your one-stop-app for kids games</h3>
      </div>
      <div className="right">
      <ul id="menu">
	
	<li class="parent"><a href="#">Filter Games</a>
	<ul class="child">			
	
		<li class="parent"><a href="#">Group<span class="expand">»</span></a>
			<ul class="child">
			<li onClick={handleAcad}><a  href=""nowrap>Academic </a></li>
			<li onClick={handleFinance}><a href="" nowrap>Financial Literacy</a></li>
			</ul>
		</li>
    <li class="parent"><a href="#">Levels<span class="expand">»</span></a>
			<ul class="child">
			<li onClick={handleStageOne}><a href="" nowrap>Key Stage 1</a></li>
			<li onClick={handleStageTwo}><a  href="" nowrap>key Stage 2</a></li>
			</ul>
		</li>
	</ul>
  
	</li>
	
</ul>
        <form onSubmit={handleSubmit}>

        <input type="search" placeholder="Search Games.." onChange={handleChange}/>

        </form>
        <div className="filter">
      
        </div>
              </div>
    </div>
  )
}

export default Navbar
