import React from 'react'
import useFetch from './useFetch'
import Content from '../components/Content'
const Home = () => {
  const { data, setData, isPending, error } = useFetch(`https://cors-anywhere.herokuapp.com/https://partners.9ijakids.com/index.php?partnerId=555776&accessToken=l0lawtvv-94bv-oi4d-u808-5ubz&action=catalogfilter`, { headers: { 'Content-Type': "origin", 'Content-Type': "x-requested-with" }});
  return (
    <div className="home">
      {error && <div>{error}</div>}
      {isPending && <div>Loading....</div>}
      {data && <Content data={data} setData={setData}/>}
      {console.log(data)}
    </div>
  )
}

export default Home
