import React from 'react'
import HeroCard from './HeroCard'

function Hero({ data }) {
  console.log(data);
  return (
    <div className="hero">

      {data.map(game => {
        const { GameDescription, GameImage, GameTitle, Group, Level, Subject, Topic } = game;
        return <HeroCard
          GameDescription={GameDescription}
          GameImage={GameImage}
          GameTitle={GameTitle}
          Group={Group}
          Subject={Subject}
          Topic={Topic}
        />
      })}
    </div>
  )
}

export default Hero
